                  </div>
                </div>
                <div class="account_right clearfix">
                  <button type="button" v-on:click="deleteListNeo('add-list-neocampaign', 'neoCampaigns', 'addNeoCampaigns', 'all')" title=""><img src="../../../assets/images/target/target_close_btn.png" alt=""></button>
                  <ul id="add-list-neocampaign">
                    <li v-for="(addNeoCampaign, index) in addNeoCampaigns" :key="index" class="sticker_btn">
                      <span>{{ addNeoCampaign.campaignname }}</span> <span @click="deleteListNeo('add-list-neocampaign', 'neoCampaigns', 'addNeoCampaigns', addNeoCampaign)" :data-number="addNeoCampaign.campaignid" title=""><img src="../../../assets/images/target/target_list_close.png" alt=""></span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <!--  -->
          <div class="cate_contents" v-if="wTab.tab3">
            <div class="account_info target_generate">
              <div class="account_title">"   " </div>
              <div>
                <ui-select :selectData="this.selectCustomer" data-key="selectCustomer" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subSelect">
                <ui-select :selectData="this.selectSub" data-key="selectSub" :onClick="selectOnClick"></ui-select>
              </div>
              <div class="account_date" v-if="subInput">
                <input type="text" v-if="subInput" v-model="unvisitedPeriod"><span></span>
