                  <ul id="add-list-csv">
                    <li v-for="(item, index) in addCsvItems" :key="index" class="sticker_btn">
                      <span>{{ item.name }}</span> <span @click="deleteAddCsvItems(item)" :data-number="item.name" title=""><img src="../../../assets/images/target/target_list_close.png" alt=""></span>
                    </li>
